import * as THREE from 'three';

function generateStrips()
{
  // console.log("sprite")
  // var spriteMap = new THREE.TextureLoader().load( "sprites/spider.png" );
  // var spriteMaterial = new THREE.SpriteMaterial( { map: spriteMap, color: 0xffffff } );
  // var sprite = new THREE.Sprite( spriteMaterial );
  // return sprite;

  var texture = new THREE.TextureLoader().load( "sprites/spider.png" );
  var geometry = new THREE.PlaneBufferGeometry( 15, 15 );
  var material = new THREE.MeshBasicMaterial( {side: THREE.BackSide} );
  // material.alphaMap = texture;
  material.transparent = true;

  // texture.encoding = THREE.sRGBEncoding;
  material.map = texture;
  material.needsUpdate = true;

  
  var mesh = new THREE.Mesh( geometry, material );
  mesh.rotateX(90 * Math.PI / 180)
  return mesh;

}

export default {
  generateStrips: generateStrips
}